<template>
  <body>
<div class="container">

<div class="messaging">
  <p></p>
      <div class="inbox_msg">
        <div class="inbox_people">
          <div class="headind_srch">
            <div class="textac">
              <h4>Диалоги</h4>
            </div>
            <div class="srch_bar">
            </div>
          </div>
          <div class="inbox_chat">
            <div class="chat_list" v-for="room in rooms"
            :key="room.id">
              <div class="chat_people">
                <div class="chat_img"> <img :src="room.ProductImage" alt="sunil"> </div>

                <router-link
                  :to="{ name: 'userRoom', params: { id: room.id }}"
                ><div class="chat_ib">
                  <h5> {{ room.Product != null ? room.Product.User.name : 'Продукт' }} {{ room.Product != null ? room.Product.User.lastname : 'удален' }}<span class="chat_date">{{ room.createdAt | fromNow }}</span></h5>
                  <p>{{ room.Product != null ? room.Product.name : 'Продукт удален'}}</p>
                </div></router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div></div>
    </body>
</template>
<script>
import { fromNowFilter } from '@/utils/mixins'
import { mapState } from "vuex";
import { Toast } from "./../utils/helpers";
import chatsAPI from "./../apis/chat";
// import productCard from "./../components/productCard";
export default {
  mixins: [fromNowFilter],

  // components: {
  //   productCard
  // },
  data() {
    return {
      rooms: [],
      cardImg: "",
      isLoading: false
    };
  },
  computed: {
    ...mapState(["currentUser"])

  },
  created() {
    this.fetchRooms();
  },
  beforeRouteUpdate(to, from, next) {
    this.fetchUserFavorite();
    next();
  },
  methods: {
    async fetchRooms() {
      try {
        this.$store.dispatch("updateLoading", true);
        const { data, statusText } = await chatsAPI.getRooms();
        if (statusText !== "OK") {
          throw new Error(statusText);
        }
        this.rooms = data.rooms;

        this.$store.dispatch("updateLoading", false);
      } catch (error) {
        this.$store.dispatch("updateLoading", false);
        Toast.fire({
          icon: "error",
          title: "Не получается получить информацию о продукте, пожалуйста, попробуйте снова позже"
        });
      }
    }
  }
};
</script>

